/**
 * Evidence/Report data management
 * All data comes from Google Sheets "evidence" tab
 */

export interface Evidence {
  id: string;
  lat: number;
  lng: number;
  category: string;
  severity: 'Low' | 'Medium' | 'High' | 'Critical';
  note: string;
  reporter: string;
  date: string;
  zone: string;
  upvotes: number;
  verified: boolean;
  status: 'Open' | 'In Progress' | 'Resolved' | 'Escalated';
  images?: string[];
  assignedTo?: string;
  assignedDate?: string;
}

/**
 * Fetch evidence from Google Sheets
 * Sheet tab name: "evidence"
 */
export async function fetchEvidenceFromSheets(): Promise<Evidence[]> {
  const SHEET_ID = process.env.NEXT_PUBLIC_GOOGLE_SHEETS_ID;
  
  if (!SHEET_ID) {
    return [];
  }

  try {
    const url = `https://docs.google.com/spreadsheets/d/${SHEET_ID}/gviz/query?tq=SELECT%20*%20FROM%20'evidence'&tqx=out:json`;
    const response = await fetch(url);
    const text = await response.text();
    
    const jsonString = text.substring(47).slice(0, -2);
    const json = JSON.parse(jsonString);
    
    const evidence = json.table.rows.map((row: any, idx: number) => {
      const cols = row.c;
      return {
        id: cols[0]?.v || `E${idx + 1}`,
        lat: parseFloat(cols[1]?.v) || 0,
        lng: parseFloat(cols[2]?.v) || 0,
        category: cols[3]?.v || '',
        severity: cols[4]?.v || 'Low',
        note: cols[5]?.v || '',
        reporter: cols[6]?.v || '',
        date: cols[7]?.v || new Date().toISOString().split('T')[0],
        zone: cols[8]?.v || '',
        upvotes: parseInt(cols[9]?.v) || 0,
        verified: cols[10]?.v?.toLowerCase() === 'yes' || cols[10]?.v?.toLowerCase() === 'true',
        status: cols[11]?.v || 'Open',
        images: (cols[12]?.v || '').split('|').filter((s: string) => s.trim()),
        assignedTo: cols[13]?.v || '',
        assignedDate: cols[14]?.v || '',
      };
    });

    return evidence;
  } catch (error) {
    console.error('Error fetching evidence from Google Sheets:', error);
    return [];
  }
}
